	module.exports = {"data":{"postsJson":{"title":"Ascension & Pentecost","questionsTitle":"COMPARISON, CONTRAST & REPETITION","display":"block","bookName":"Acts","ref":"1:1-2:47","html":"<p>   <strong>1</strong><u>1</u>The first book I wrote, Theophilus, concerned all that Jesus began both to do and to teach, <u>2</u>until the day in which he was received up, after he had given commandment through the Holy Spirit to the apostles whom he had chosen. <u>3</u>To these he also showed himself alive after he suffered, by many proofs, appearing to them over a period of forty days, and speaking about God’s Kingdom. <u>4</u>Being assembled together with them, he commanded them,</p><h1>JESUS</h1><h2>Don’t depart from Jerusalem, but wait for the promise of the Father, which you heard from me. <u>5</u>For John indeed baptized in water, but you will be baptized in the Holy Spirit not many days from now.</h2><p>   <u>6</u>Therefore when they had come together, they asked him,</p><h1>ELEVEN DISCIPLES</h1><h3>Lord, are you now restoring the kingdom to Israel?</h3><p>   <u>7</u>He said to them,</p><h1>JESUS</h1><h2>It isn’t for you to know times or seasons which the Father has set within his own authority. <u>8</u>But you will receive power when the Holy Spirit has come upon you. You will be witnesses to me in Jerusalem, in all Judea and Samaria, and to the uttermost parts of the earth.</h2><p>   <u>9</u>When he had said these things, as they were looking, he was taken up, and a cloud received him out of their sight. <u>10</u>While they were looking steadfastly into the sky as he went, behold,* two men stood by them in white clothing, <u>11</u>who also said,</p><h1>TWO ANGELS</h1><h4>You men of Galilee, why do you stand looking into the sky? This Jesus, who was received up from you into the sky will come back in the same way as you saw him going into the sky.</h4><p>   <u>12</u>Then they returned to Jerusalem from the mountain called Olivet, which is near Jerusalem, a Sabbath day’s journey away. <u>13</u>When they had come in, they went up into the upper room, where they were staying; that is Peter, John, James, Andrew, Philip, Thomas, Bartholomew, Matthew, James the son of Alphaeus, Simon the Zealot, and Judas the son of James. <u>14</u>All these with one accord continued steadfastly in prayer and supplication, along with the women, and Mary the mother of Jesus, and with his brothers.<br />   <u>15</u>In these days, Peter stood up in the middle of the disciples (and the number of names was about one hundred twenty), and said,</p><h1>SIMON PETER</h1><h3><u>16</u>Brothers, it was necessary that this Scripture should be fulfilled, which the Holy Spirit spoke before by the mouth of David concerning Judas, who was guide to those who took Jesus. <u>17</u>For he was numbered with us, and received his portion in this ministry. <u>18</u>Now this man obtained a field with the reward for his wickedness, and falling headlong, his body burst open, and all his intestines gushed out. <u>19</u>It became known to everyone who lived in Jerusalem that in their language that field was called ‘Akeldama,’ that is, ‘The field of blood.’ <u>20</u>For it is written in the book of Psalms,<br />   ‘Let his habitation be made desolate.<br />   Let no one dwell therein;’*<br />   and,<br />   ‘Let another take his office.’*<br />   <u>21</u>Of the men therefore who have accompanied us all the time that the Lord Jesus went in and out among us, <u>22</u>beginning from the baptism of John, to the day that he was received up from us, of these one must become a witness with us of his resurrection.</h3><p>   <u>23</u>They put forward two, Joseph called Barsabbas, who was also called Justus, and Matthias. <u>24</u>They prayed, and said,</p><h1>BELIEVERS IN JERUSALEM</h1><h4>You, Lord, who know the hearts of all men, show which one of these two you have chosen <u>25</u>to take part in this ministry and apostleship from which Judas fell away, that he might go to his own place.</h4><p><u>26</u>They drew lots for them, and the lot fell on Matthias, and he was numbered with the eleven apostles.<br />   <strong>2</strong><u>1</u>Now when the day of Pentecost had come, they were all with one accord in one place. <u>2</u>Suddenly there came from the sky a sound like the rushing of a mighty wind, and it filled all the house where they were sitting. <u>3</u>Tongues like fire appeared and were distributed to them, and one sat on each of them. <u>4</u>They were all filled with the Holy Spirit, and began to speak with other languages, as the Spirit gave them the ability to speak. <u>5</u>Now there were dwelling in Jerusalem Jews, devout men, from every nation under the sky. <u>6</u>When this sound was heard, the multitude came together, and were bewildered, because everyone heard them speaking in his own language. <u>7</u>They were all amazed and marveled, saying to one another,</p><h1>JEWS FROM MANY NATIONS</h1><h4>Behold, aren’t all these who speak Galileans? <u>8</u>How do we hear, everyone in our own native language? <u>9</u>Parthians, Medes, Elamites, and people from Mesopotamia, Judea, Cappadocia, Pontus, Asia, <u>10</u>Phrygia, Pamphylia, Egypt, the parts of Libya around Cyrene, visitors from Rome, both Jews and proselytes, <u>11</u>Cretans and Arabians: we hear them speaking in our languages the mighty works of God!</h4><p><u>12</u>They were all amazed, and were perplexed, saying to one another,</p><h1>JEWS FROM MANY NATIONS</h1><h4>What does this mean?</h4><p><u>13</u>Others, mocking, said,</p><h1>JEWS FROM MANY NATIONS</h1><h4>They are filled with new wine.</h4><p>   <u>14</u>But Peter, standing up with the eleven, lifted up his voice, and spoke out to them,</p><h1>SIMON PETER</h1><h3>You men of Judea, and all you who dwell at Jerusalem, let this be known to you, and listen to my words. <u>15</u>For these aren’t drunken, as you suppose, seeing it is only the third hour of the day.* <u>16</u>But this is what has been spoken through the prophet Joel:<br />   <u>17</u>‘It will be in the last days, says God,<br />   that I will pour out my Spirit on all flesh.<br />   Your sons and your daughters will prophesy.<br />   Your young men will see visions.<br />   Your old men will dream dreams.<br />   <u>18</u>Yes, and on my servants and on my handmaidens in those days,<br />   I will pour out my Spirit, and they will prophesy.<br />   <u>19</u>I will show wonders in the sky above,<br />   and signs on the earth beneath;<br />   blood, and fire, and billows of smoke.<br />   <u>20</u>The sun will be turned into darkness,<br />   and the moon into blood,<br />   before the great and glorious day of the Lord comes.<br />   <u>21</u>It will be that whoever will call on the name of the Lord will be saved.’*<br />   <u>22</u>Men of Israel, hear these words! Jesus of Nazareth, a man approved by God to you by mighty works and wonders and signs which God did by him among you, even as you yourselves know, <u>23</u>him, being delivered up by the determined counsel and foreknowledge of God, you have taken by the hand of lawless men, crucified and killed; <u>24</u>whom God raised up, having freed him from the agony of death, because it was not possible that he should be held by it. <u>25</u>For David says concerning him,<br />   ‘I saw the Lord always before my face,<br />   For he is on my right hand, that I should not be moved.<br />   <u>26</u>Therefore my heart was glad, and my tongue rejoiced.<br />   Moreover my flesh also will dwell in hope;<br />   <u>27</u>because you will not leave my soul in Hades,*<br />   neither will you allow your Holy One to see decay.<br />   <u>28</u>You made known to me the ways of life.<br />   You will make me full of gladness with your presence.’*<br />   <u>29</u>Brothers, I may tell you freely of the patriarch David, that he both died and was buried, and his tomb is with us to this day. <u>30</u>Therefore, being a prophet, and knowing that God had sworn with an oath to him that of the fruit of his body, according to the flesh, he would raise up the Christ to sit on his throne, <u>31</u>he foreseeing this spoke about the resurrection of the Christ, that neither was his soul left in Hades,* nor did his flesh see decay. <u>32</u>This Jesus God raised up, to which we all are witnesses. <u>33</u>Being therefore exalted by the right hand of God, and having received from the Father the promise of the Holy Spirit, he has poured out this, which you now see and hear. <u>34</u>For David didn’t ascend into the heavens, but he says himself,<br />   ‘The Lord said to my Lord, “Sit by my right hand,<br />   <u>35</u>until I make your enemies a footstool for your feet.”’*<br />   <u>36</u>Let all the house of Israel therefore know certainly that God has made him both Lord and Christ, this Jesus whom you crucified.</h3><p>   <u>37</u>Now when they heard this, they were cut to the heart, and said to Peter and the rest of the apostles,</p><h1>JEWS FROM MANY NATIONS</h1><h4>Brothers, what shall we do?</h4><p>   <u>38</u>Peter said to them,</p><h1>SIMON PETER</h1><h3>Repent, and be baptized, every one of you, in the name of Jesus Christ for the forgiveness of sins, and you will receive the gift of the Holy Spirit. <u>39</u>For the promise is to you, and to your children, and to all who are far off, even as many as the Lord our God will call to himself.</h3><p><u>40</u>With many other words he testified, and exhorted them, saying,</p><h1>SIMON PETER</h1><h3>Save yourselves from this crooked generation!</h3><p>   <u>41</u>Then those who gladly received his word were baptized. There were added that day about three thousand souls. <u>42</u>They continued steadfastly in the apostles’ teaching and fellowship, in the breaking of bread, and prayer. <u>43</u>Fear came on every soul, and many wonders and signs were done through the apostles. <u>44</u>All who believed were together, and had all things in common. <u>45</u>They sold their possessions and goods, and distributed them to all, according as anyone had need. <u>46</u>Day by day, continuing steadfastly with one accord in the temple, and breaking bread at home, they took their food with gladness and singleness of heart, <u>47</u>praising God, and having favor with all the people. The Lord added to the assembly day by day those who were being saved.</p><h5>*Acts 1:10 “Behold”, from “ἰδοὺ”, means look at, take notice, observe, see, or gaze at. It is often used as an interjection.<br />*Acts 1:20 Psalm 69:25<br />*Acts 1:20 Psalm 109:8<br />*Acts 2:15 about 9:00 AM<br />*Acts 2:21 Joel 2:28-32<br />*Acts 2:27 or, Hell<br />*Acts 2:28 Psalm 16:8-11<br />*Acts 2:31 or, Hell<br />*Acts 2:35 Psalm 110:1</h5>","path":"/352","Question1":"To compare is to associate two or more similar persons, ideas, or situations. Can you discover any comparisons made by the author of this story? Why do you think that the author would have made this comparison?","Question2":"To contrast is to put side by side two or more distinct persons, ideas, or situations. Can you discover any contrasts made by the author of this story? Why do you think that the author would have made this contrast?","Question3":"Take time to note any repeated words or concepts you have found in this story.","Question4":"Please take time to write a short prayer to God responding to what you have discovered through these comparisons, contrasts and repetitions.","prevPath":"/351","nextPath":"/353"}},"pathContext":{}}

